import {Container} from 'react-bootstrap';
import Login from './Components/Login';
import Home from './Components/Home';
import Users from './Components/Users';
import AggregateCalculatorHome from './Components/AggregateCalculatorHome';
import AggregateDetails from './Components/AggregateDetails';
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import {ToastContainer} from 'react-toastify';
import { useContextApi } from './ContexApi';
import { useEffect } from 'react';
import QuizBank from './Components/QuizBank';
import YourProfile from './Components/YourProfile';
import GroupFormer from './Components/GroupFormer';
import GroupFormerDetailed from './Components/GroupFormerDetailed';
import ContextProvider from './ContexApi';
import axios from 'axios';

import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-toastify/dist/ReactToastify.css';
import './App.css';
function App() {
  const { userData, setUserData, token, setToken,login, setLogin, useLoclHost } = useContextApi();
  useEffect(()=>{
    let tempTok = localStorage.getItem("token");
    setLogin(localStorage.getItem("token") != null);
    if (tempTok != null) {
        axios.defaults.headers.common['Authorization'] = `Bearer ${tempTok}`;
        axios.get(((useLoclHost?"http://localhost:2028":"")+"/api/user/")).then((res) => {
            if (res.status == 200) {
                setUserData(res.data);
                setLogin(true);
                setToken(tempTok);
            }
        }).catch((error)=>{
          setLogin(false);
          localStorage.removeItem("token");
          setToken(null);
          setUserData(null);
          console.log(error);
        });
    }

  }, [token, login])
  return (
      <>
        <ToastContainer />
        <Router>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/login" element={<Login />} />
            <Route path="/users" element={<Users />} />
            <Route path="/aggregate-calculator" element={<AggregateCalculatorHome />} />
            <Route path="/aggregate-calculator/:id" element={<AggregateDetails />} />
            <Route path="/quiz-bank" element={<QuizBank />} />
            <Route path="/your-profile" element={<YourProfile />} />
            <Route path="/group-former" element={<GroupFormer />} />
            <Route path="/group-former/:id" element={<GroupFormerDetailed />} />
          </Routes>
        </Router>
      </>
  );
}

export default App;
