import React, {useState, useEffect, useRef} from "react";

import { useNavigate } from "react-router-dom";
import { useContextApi } from "../ContexApi";
import { toast } from "react-toastify";
import { Button, Container, Modal, Table, Form, Card } from "react-bootstrap";

import axios from "axios";

import logo from '../Assets/logo.PNG';

export default function AggregateCalculatorHome(){

    const {token, setToken, login, setLogin, userData, useLoclHost} = useContextApi();
    const [subjects, setSubjects] = useState([]);
    const [addMod, setAddMod] = useState(false);
    const [formData, setFormData] = useState({
        name:"",
        credits:0,
        quiz_weightage:0,
        assignment_weightage:0,
        mids_weightage:0,
        finals_weightage:0
    });
    const navigate = useNavigate();

    const fetchSubjects = () => {
        axios.get((useLoclHost?"http://localhost:2028/":"")+"api/subjects/").then((res) => {
            if (res.status == 200) {
                setSubjects(res.data.subjects);
            }
        }).catch((err) => {
            console.log(err);
        });
    }

    const handleAddCourse = () => {
        axios.post((useLoclHost?"http://localhost:2028/":"")+"api/subjects/", {
            name: formData.name,
            credits: formData.credits,
            quiz_weightage: formData.quiz_weightage,
            assignment_weightage: formData.assignment_weightage,
            mids_weightage: formData.mids_weightage,
            finals_weightage: formData.finals_weightage
        }).then((res) => {
            if (res.status == 200) {
                toast.success("Subject Added Successfully!");
                setAddMod(false);
                fetchSubjects();
            }
        }).catch((err) => {
            console.log(err);
        });
    }

    const handleDeleteCourse = (id) => {
        axios.delete((useLoclHost?"http://localhost:2028/":"")+"api/subjects/"+id+"/").then((res) => {
            if (res.status == 200) {
                toast.success("Subject Deleted Successfully!");
                setAddMod(false);
                fetchSubjects();
            }
        }).catch((err) => {
            toast.error("Something went wrong!");
        })
    }

    useEffect(() => {
        if (token){
            fetchSubjects();
        }
    }, [token, login])

    return(
        <div style={{display:'flex', alignItems:'stretch', height:'100%'}}>
            <Container className="sidebar" style={{maxWidth: "100%", backgroundColor: "#2463AB", width: "20%", padding: '0 !important', margin:'0 !important'}}>
                <p className="h2 text-center mt-5" style={{color:'white'}}>Navigation</p>
                <Container className="sidebar-links">
                    <p className="nav-btn text-center" onClick={()=>navigate("/")}>Home</p>
                    {userData?.isAdmin && <p className="nav-btn text-center" onClick={()=>navigate("/users")}>Users</p>}
                    <p className="nav-btn text-center active" onClick={()=>navigate("/aggregate-calculator")}>Aggregate Calculator</p>
                    <p className="nav-btn text-center" onClick={()=> navigate("/group-former")}>Group Former</p>
                    <p className="nav-btn text-center" onClick={()=>navigate("/quiz-bank")}>Quiz Bank</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/your-profile")}>Your Profile</p>
                    <p className="nav-btn text-center" onClick={() => {setToken(null); localStorage.removeItem("token"); setLogin(false); navigate("/login")}}><strong>Logout</strong></p>
                </Container>
            </Container>
            <Container className="main" style={{padding:'0', margin:'0', minHeight:'100vh'}}>
                <Container className="header" style={{width:'100%', border:'1px solid black', padding: '0', margin:'0', maxHeight:'15%', height:'100px'}}>
                    <Container style={{padding: '20px', textAlign:'center', display:'flex', alignItems:'center', justifyContent:'center'}}>
                        <img src={logo} style={{width:'50px', height:'50px', marginRight:'10px'}} alt="logo" />
                        <p className="h1">StudentSync</p>
                    </Container>
                </Container>
                <Container className="mainContent" style={{backgroundColor:'#D4D4D4', minHeight:'100vh',display:'flex',flexDirection:'column'}}>
                    <Container className="text-center mt-4">
                        <h2>Your Courses</h2>
                        <p>Please select a course to calculate your aggreagate in.</p>
                    </Container>
                    {userData?.isAdmin && <Container className="admin-btns" style={{display:'flex', justifyContent:'center', margin:'10px'}}>
                        <Button onClick={() => setAddMod(true)} style={{backgroundColor:'#2463AB'}}>Add Course</Button>
                    </Container>}
                    <Container style={{display:'flex', justifyContent:'center', flexWrap:'wrap'}}>
                        {
                            subjects.map((subject, index) => (
                                <Card style={{ margin:'10px', maxWidth:'500px' }} className="zoomable-card">
                                    <Card.Header style={{display:'flex'}}>
                                        <Container>
                                            <Card.Title style={{fontSize:'18px'}}>{subject.name}</Card.Title>
                                            <Card.Subtitle className="mb-2 text-muted">
                                                <strong>
                                                    Credit Hours:&nbsp;
                                                </strong>
                                                {subject.credits}
                                            </Card.Subtitle>
                                        </Container>
                                        {
                                            userData?.isAdmin && 
                                            <Button variant="danger" size="sm" style={{height:'fit-content'}} onClick={()=>handleDeleteCourse(subject.id)}>
                                                X
                                            </Button>
                                        }
                                    </Card.Header>
                                    <Card.Body>
                                        <Card.Text> 
                                            <Container style={{display:'flex', justifyContent:'space-between', flexWrap:'wrap'}}>
                                                <span>
                                                    Quiz Weightage:  {subject.quiz_weightage}&nbsp;
                                                </span>
                                                <span>
                                                    Assignment Weightage: {subject.assignment_weightage}&nbsp;
                                                </span>
                                                <span>
                                                    Final Exams Weightage: {subject.finals_weightage}&nbsp;
                                                </span>
                                                <span>
                                                    Mids Weightage: {subject.mids_weightage}&nbsp;
                                                </span>
                                            </Container>
                                        </Card.Text>
                                    </Card.Body>
                                    <Card.Footer style={{display:'flex', justifyContent:'flex-end'}}>
                                        <Button variant="primary" size="sm" onClick={()=>navigate("/aggregate-calculator/"+subject.id)}>Calculate</Button>
                                    </Card.Footer>
                                </Card>
                            ))
                        }
                    </Container>
                </Container>
                <Container>
                    <Modal show={addMod} onHide={() => setAddMod(false)}>
                        <Modal.Header closeButton>
                            <Modal.Title>
                                Add Course
                            </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Form>
                                <Form.Group className="mb-3">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control type="text" placeholder="Enter course name" onChange={(e) => setFormData({...formData, name:e.target.value})}/>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>Credits</Form.Label>
                                    <Form.Control type="number" placeholder="Enter credit hours" onChange={(e) => setFormData({...formData, credits:e.target.value})}/>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>Quiz Weightage</Form.Label>
                                    <Form.Control type="number" placeholder="Enter quiz weightage" onChange={(e) => setFormData({...formData, quiz_weightage:e.target.value})}/>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>Assignment Weightage</Form.Label>
                                    <Form.Control type="number" placeholder="Enter assignment weightage" onChange={(e) => setFormData({...formData, assignment_weightage:e.target.value})}/>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>Mids Weightage</Form.Label>
                                    <Form.Control type="number" placeholder="Enter mids weightage" onChange={(e) => setFormData({...formData, mids_weightage:e.target.value})}/>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>Finals Weightage</Form.Label>
                                    <Form.Control type="number" placeholder="Enter finals weightage" onChange={(e) => setFormData({...formData, finals_weightage:e.target.value})}/>
                                </Form.Group>
                            </Form>
                        </Modal.Body>
                        <Modal.Footer>
                            <Container style={{display:'flex', justifyContent:'flex-end', width:'100%'}}>
                                 <Button variant="secondary" onClick={()=>setAddMod(false)}>
                                    Close
                                </Button>
                                <Button variant="primary" style={{backgroundColor:'#2463AB', marginLeft:'3px'}} onClick={()=>handleAddCourse()}>
                                    Add Course
                                </Button>
                            </Container>
                        </Modal.Footer>
                    </Modal>
                </Container>
            </Container>
        </div>
    )
}