import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useContextApi } from "../ContexApi";
import { toast } from "react-toastify";
import { Button, Container, Card, Form, Modal } from "react-bootstrap";
import axios from "axios";

import logo from '../Assets/logo.PNG';

export default function GroupFormer() {
    const { token, setToken, login, setLogin, userData, useLoclHost } = useContextApi();
    const navigate = useNavigate();
    const [selectedSub, setSelectedSub] = useState("");
    const [showModal, setShowModal] = useState(false);
    const [formData, setFormData] = useState({
        sessionName:"",
        groupMembers:0
    });
    const [sessions, setSessions] = useState([]);

    const fetchSessions = () => {
        axios.get(`${useLoclHost ? "http://localhost:2028" : ""}/api/grouping-sessions/`)
        .then((response) => {
            setSessions(response.data);
        })
    }

    const handleStartSession = () => {
        setShowModal(false);
        axios.post(`${useLoclHost ? "http://localhost:2028" : ""}/api/grouping-sessions/`, formData)
        .then((response) => {
            toast.success("Session Started");
            fetchSessions();
        })
    }

    const handleDelete = (id) => {
        axios.delete(`${useLoclHost ? "http://localhost:2028" : ""}/api/grouping-sessions/${id}/`)
        .then((response) => {
            toast.success("Session Deleted");
            fetchSessions();
        })
    }
    

    useEffect(() => {
        if (token){
            fetchSessions();
        }
    }, []);


    return (
        <div style={{ display: 'flex', alignItems: 'stretch', height: '100%' }}>
            <Container className="sidebar" style={{ maxWidth: "100%", backgroundColor: "#2463AB", width: "20%", padding: '0 !important', margin: '0 !important' }}>
                <p className="h2 text-center mt-5" style={{ color: 'white' }}>Navigation</p>
                <Container className="sidebar-links">
                    <p className="nav-btn text-center" onClick={() => navigate("/")}>Home</p>
                    {userData?.isAdmin && <p className="nav-btn text-center" onClick={() => navigate("/users")}>Users</p>}
                    <p className="nav-btn text-center" onClick={() => navigate("/aggregate-calculator")}>Aggregate Calculator</p>
                    <p className="nav-btn text-center active" onClick={()=> navigate("/group-former")}>Group Former</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/quiz-bank")}>Quiz Bank</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/your-profile")}>Your Profile</p>
                    <p className="nav-btn text-center" onClick={() => { setToken(null); localStorage.removeItem("token"); setLogin(false); navigate("/login") }}><strong>Logout</strong></p>
                </Container>
            </Container>
            <Container className="main" style={{ padding: '0', margin: '0', minHeight: '100vh' }}>
                <Container className="header" style={{ width: '100%', border: '1px solid black', padding: '0', margin: '0', maxHeight: '15%', height: '100px' }}>
                    <Container style={{ padding: '20px', textAlign: 'center', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <img src={logo} style={{ width: '50px', height: '50px', marginRight: '10px' }} alt="logo" />
                        <p className="h1">StudentSync</p>
                    </Container>
                </Container>
                <Container className="mainContent" style={{ backgroundColor: '#D4D4D4', minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
                    <Container style={{ marginTop: '50px' }}>
                        <h2 className="text-center">Group Forming Sessions</h2>
                        <p className="text-center text-muted">You can view the results of past, vote in ongoing group forming sessions and <br />start new group forming sessions here</p>
                    </Container>
                    {userData?.isAdmin && <Container style={{display:'flex', justifyContent:'center'}}>
                        <Button variant="primary" style={{backgroundColor:"#2463AB"}} onClick={() => setShowModal(true)}>Start New Session</Button>
                    </Container>}
                    <Container style={{ margin: '20px' }}>
                        <h2 className="text-center m-2 mb-3">Ongoing Group Forming Sessions</h2>
                        {
                            sessions?.map((session) => (
                                session.status === "Ongoing" && (<Card key={session.id} style={{ width: '50%', margin: '0 auto', marginBottom: '20px' }} className="zoomable-card">
                                    <Card.Header style={{display:'flex', justifyContent:'flex-end'}}>
                                        {userData?.isAdmin && <Button variant="danger" size="sm" onClick={()=>handleDelete(session.id)}>
                                            X
                                        </Button>}
                                    </Card.Header>
                                    <Card.Body onClick={() => navigate(`/group-former/${session.id}`)}>
                                            <Card.Title>{session.sessionName}</Card.Title>
                                            <Card.Text>Group Members: {session.membersPerGroup}</Card.Text>
                                    </Card.Body>
                                </Card>)
                            ))
                        }
                    </Container>
                    <Container style={{ margin: '20px' }}>
                        <h2 className="text-center m-2 mb-3">Completed Forming Sessions</h2>
                        {
                            sessions?.map((session) => (
                                session.status === "Completed" && (<Card key={session.id} style={{ width: '50%', margin: '0 auto', marginBottom: '20px' }} className="zoomable-card">
                                    <Card.Header style={{display:'flex', justifyContent:'flex-end'}}>
                                        {userData?.isAdmin && <Button variant="danger" size="sm" onClick={()=>handleDelete(session.id)}>
                                            X
                                        </Button>}
                                    </Card.Header>
                                    <Card.Body onClick={() => navigate(`/group-former/${session.id}`)}>
                                            <Card.Title>{session.sessionName}</Card.Title>
                                            <Card.Text>Group Members: {session.membersPerGroup}</Card.Text>
                                    </Card.Body>
                                </Card>)
                            ))
                        }
                    </Container>
                </Container>
                <Container className="modal-container">
                    <Modal show={showModal} onHide={() => setShowModal(false)}>
                        <Modal.Header closeButton>
                            <Modal.Title>Start New Group Forming Session</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Form>
                                <Form.Group className="mb-3">
                                    <Form.Label>Session Name</Form.Label>
                                    <Form.Control type="text" placeholder="Session Name" onChange={(e) => setFormData({ ...formData, sessionName: e.target.value })} value={formData.sessionName} />
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label>Members Per Group</Form.Label>
                                    <Form.Control type="number" placeholder="Group Members" onChange={(e) => setFormData({ ...formData, groupMembers: e.target.value })} value={formData.groupMembers} />
                                </Form.Group>
                            </Form>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={() => setShowModal(false)}>
                                Close
                            </Button>
                            <Button variant="primary" onClick={handleStartSession}>
                                Start Session
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </Container>
            </Container>
        </div>
    );
}
