import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useContextApi } from "../ContexApi";
import { toast } from "react-toastify";
import { Button, Container, Card, Form, Modal } from "react-bootstrap";
import axios from "axios";

import logo from '../Assets/logo.PNG';

export default function YourProfile() {
    const { token, setToken, login, setLogin, userData, useLoclHost } = useContextApi();
    const [newPassword, setNewPassword] = useState({ oldPassword: "", newPassword: "", confirmNewPassword: "" });
    const [showModal, setShowModal] = useState(false);
    const navigate = useNavigate();


    // Function to handle password change
    const handlePasswordChange = () => {
        if (newPassword.newPassword !== newPassword.confirmNewPassword) {
            toast.error("New passwords do not match!");
            return;
        }

        axios.post(useLoclHost?"http://localhost:2028":""+"/api/change-password/", {
            oldPassword: newPassword.oldPassword,
            newPassword: newPassword.newPassword,
        }, {
            headers: {
                Authorization: `Bearer ${token}`
            }
        })
            .then((res) => {
                if (res.status === 200) {
                    toast.success("Password changed successfully!");
                    setNewPassword({ oldPassword: "", newPassword: "", confirmNewPassword: "" });
                    setShowModal(false);
                }
            })
            .catch((err) => {
                console.error(err);
                toast.error("Failed to change password.");
            });
    };

    return (
        <div style={{ display: 'flex', alignItems: 'stretch', height: '100%' }}>
            <Container className="sidebar" style={{ maxWidth: "100%", backgroundColor: "#2463AB", width: "20%", padding: '0 !important', margin: '0 !important' }}>
                <p className="h2 text-center mt-5" style={{ color: 'white' }}>Navigation</p>
                <Container className="sidebar-links">
                    <p className="nav-btn text-center" onClick={() => navigate("/")}>Home</p>
                    {userData?.isAdmin && <p className="nav-btn text-center" onClick={() => navigate("/users")}>Users</p>}
                    <p className="nav-btn text-center" onClick={() => navigate("/aggregate-calculator")}>Aggregate Calculator</p>
                    <p className="nav-btn text-center" onClick={()=> navigate("/group-former")}>Group Former</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/quiz-bank")}>Quiz Bank</p>
                    <p className="nav-btn text-center active" onClick={() => navigate("/your-profile")}>Your Profile</p>
                    <p className="nav-btn text-center" onClick={() => { setToken(null); localStorage.removeItem("token"); setLogin(false); navigate("/login") }}><strong>Logout</strong></p>
                </Container>
            </Container>
            <Container className="main" style={{ padding: '0', margin: '0', minHeight: '100vh' }}>
                <Container className="header" style={{ width: '100%', border: '1px solid black', padding: '0', margin: '0', maxHeight: '15%', height: '100px' }}>
                    <Container style={{ padding: '20px', textAlign: 'center', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <img src={logo} style={{ width: '50px', height: '50px', marginRight: '10px' }} alt="logo" />
                        <p className="h1">StudentSync</p>
                    </Container>
                </Container>
                <Container className="mainContent" style={{ backgroundColor: '#D4D4D4', minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
                    <Container className="text-center mt-4">
                        <h2>Your Profile</h2>
                        <Container className="mt-3">
                            <p style={{fontSize:'20px'}}>
                                <span style={{ fontWeight: 'bold' }}>Username/Email: </span>{userData?.email}
                                <br />
                                <span style={{ fontWeight: 'bold' }}>Name: </span>{userData?.name}
                            </p>
                        </Container>
                    </Container>

                    <Container className="text-center mt-1">
                        <Button variant="primary" onClick={() => setShowModal(true)} style={{ backgroundColor: '#2463AB' }}>Change Password</Button>

                        {/* Modal for changing password */}
                        <Modal show={showModal} onHide={() => setShowModal(false)}>
                            <Modal.Header closeButton>
                                <Modal.Title>Change Password</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <Form.Group>
                                        <Form.Label>Old Password</Form.Label>
                                        <Form.Control
                                            type="password"
                                            value={newPassword.oldPassword}
                                            onChange={(e) => setNewPassword({ ...newPassword, oldPassword: e.target.value })}
                                        />
                                    </Form.Group>
                                    <Form.Group>
                                        <Form.Label>New Password</Form.Label>
                                        <Form.Control
                                            type="password"
                                            value={newPassword.newPassword}
                                            onChange={(e) => setNewPassword({ ...newPassword, newPassword: e.target.value })}
                                        />
                                    </Form.Group>
                                    <Form.Group>
                                        <Form.Label>Confirm New Password</Form.Label>
                                        <Form.Control
                                            type="password"
                                            value={newPassword.confirmNewPassword}
                                            onChange={(e) => setNewPassword({ ...newPassword, confirmNewPassword: e.target.value })}
                                        />
                                    </Form.Group>
                                </Form>
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={() => setShowModal(false)}>Close</Button>
                                <Button variant="primary" style={{ backgroundColor: '#2463AB' }} onClick={handlePasswordChange}>Change Password</Button>
                            </Modal.Footer>
                        </Modal>
                    </Container>
                </Container>
            </Container>
        </div>
    );
}
