import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useContextApi } from "../ContexApi";
import { toast } from "react-toastify";
import { Button, Container, Form, Table } from "react-bootstrap";
import axios from "axios";
import { useParams } from "react-router-dom";

import logo from "../Assets/logo.PNG";

export default function GroupFormerDetailed() {
    const { token, setToken, login, setLogin, userData, useLoclHost } = useContextApi();
    const navigate = useNavigate();
    const { id } = useParams();
    const [session, setSession] = useState({});
    const [users, setUsers] = useState([]);
    const [selectedUsers, setSelectedUsers] = useState([]);
    const [searchQuery, setSearchQuery] = useState("");
    const maxSelection = session.membersPerGroup ? session.membersPerGroup - 1 : 1; // Members - 1
    const [filteredUsers, setFilteredUsers] = useState([]);
    const [groups, setGroups] = useState([]);

    const fetchSessionData = () => {
        axios.get(`${useLoclHost ? "http://localhost:2028" : ""}/api/grouping-sessions/${id}/`)
            .then((response) => {
                setSession(response.data);
                console.log(response.data);
            });
    };

    const fetchGroups = (id, sessionName) => {
        console.log(id, sessionName);
        axios.post(`${useLoclHost ? "http://localhost:2028" : ""}/api/grouping-sessions/groups/`, {
            sessionName: sessionName,
            id: id
        })
        .then((res) => {
            if (res.status === 200) {
                setGroups(res.data);
            }
        })
        console.log("yay");
    }

    const fetchUsers = () => {
        axios.get(`${useLoclHost ? "http://localhost:2028" : ""}/api/users/`)
            .then((res) => {
                if (res.status === 200) {
                    setUsers(res.data.users);
                    setFilteredUsers(res.data.users); // Initialize filteredUsers with all users
                }
            })
            .catch((err) => {
                console.log(err);
            });
    };

    useEffect(() => {
        if (token) {
            fetchSessionData();
            fetchUsers();
        }
    }, []);

    useEffect(() => {
        if (session.sessionName) {
            fetchGroups(id, session.sessionName);
        }
    }, [session]);

    const handleSelectUser = (userId) => {
        if (selectedUsers.includes(userId)) {
            setSelectedUsers(selectedUsers.filter((id) => id !== userId));
        } else if (selectedUsers.length < maxSelection) {
            setSelectedUsers([...selectedUsers, userId]);
        } else {
            toast.warning(`You can only select up to ${maxSelection} users.`);
        }
    };

    const handleSearch = (e) => {
        const query = e.target.value.toLowerCase();
        setSearchQuery(query);
        if (query.trim() === "") {
            setFilteredUsers(users.slice(0, 5)); // Default to the top 5 users
        } else {
            const filtered = users
                .filter((user) =>
                    user.name.toLowerCase().includes(query) ||
                    user.email.toLowerCase().includes(query)
                )
                .slice(0, 5); // Limit to top 5 results
            setFilteredUsers(filtered);
        }
    };

    const handleGroupFormation = () => {
        axios.post(`${useLoclHost ? "http://localhost:2028" : ""}/api/form-groups/`, {
            sessionId: id,
            sessionName: session.sessionName,
            membersPerGroup: session.membersPerGroup,
        })
        .then((res) => {
            if (res.status === 200) {
                toast.success("Group formation started... It will take sometime");
                fetchSessionData();
            }
        })
    }

    const handleSubmit = () => {
        if (selectedUsers.length !== maxSelection) {
            toast.error(`Please select exactly ${maxSelection} users.`);
            return;
        }

        console.log(selectedUsers)
        // Submit the preferences
        axios.post(`${useLoclHost ? "http://localhost:2028" : ""}/api/group-preferences/`, {
            sessionId: id,
            sessionName: session.sessionName,
            preferences: selectedUsers,
            email: userData?.email
        }, {
            headers: {
                Authorization: `Bearer ${token}`
            }
        }).then((res) => {
            if (res.status === 200) {
                toast.success("Preferences submitted successfully!");
                navigate("/group-former");
            }
        }).catch((err) => {
            console.log(err);
            toast.error("Failed to submit preferences.");
        });
    };

    return (
        <div style={{ display: "flex", alignItems: "stretch", height: "100%" }}>
            <Container className="sidebar" style={{ maxWidth: "100%", backgroundColor: "#2463AB", width: "20%", padding: "0 !important", margin: "0 !important" }}>
                <p className="h2 text-center mt-5" style={{ color: "white" }}>Navigation</p>
                <Container className="sidebar-links">
                    <p className="nav-btn text-center" onClick={() => navigate("/")}>Home</p>
                    {userData?.isAdmin && <p className="nav-btn text-center" onClick={() => navigate("/users")}>Users</p>}
                    <p className="nav-btn text-center" onClick={() => navigate("/aggregate-calculator")}>Aggregate Calculator</p>
                    <p className="nav-btn text-center active" onClick={() => navigate("/group-former")}>Group Former</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/quiz-bank")}>Quiz Bank</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/your-profile")}>Your Profile</p>
                    <p className="nav-btn text-center" onClick={() => { setToken(null); localStorage.removeItem("token"); setLogin(false); navigate("/login"); }}>
                        <strong>Logout</strong>
                    </p>
                </Container>
            </Container>
            <Container className="main" style={{ padding: "0", margin: "0", minHeight: "100vh" }}>
                <Container className="header" style={{ width: "100%", border: "1px solid black", padding: "0", margin: "0", maxHeight: "15%", height: "100px" }}>
                    <Container style={{ padding: "20px", textAlign: "center", display: "flex", alignItems: "center", justifyContent: "center" }}>
                        <img src={logo} style={{ width: "50px", height: "50px", marginRight: "10px" }} alt="logo" />
                        <p className="h1">StudentSync</p>
                    </Container>
                </Container>
                <Container className="mainContent" style={{ backgroundColor: "#D4D4D4", minHeight: "100vh", display: "flex", flexDirection: "column" }}>
                    <Container style={{ marginTop: "50px" }}>
                        <h2 className="text-center">{session.sessionName}</h2>
                        <h4 className="text-center">Group Members: {session.membersPerGroup}</h4>
                        <h4 className="text-center">Session Status: {session.status}</h4>
                       {session?.status == "Ongoing" && <h4 className="text-center">Preference Given? {session.preferenceGiven ? "Yes" : "No"}</h4>}
                    </Container>
                    {userData?.isAdmin && session?.status == "Ongoing" && <Container className="m-3 text-center">
                        <Button style={{ backgroundColor: "#2463AB", color: "white" }} onClick={handleGroupFormation}>
                            Form Groups
                        </Button>
                    </Container>}
                    {(!session?.preferenceGiven && session?.status == "Ongoing") && <Container style={{ marginTop: "20px", width:'70%' }}>
                        <h2 className="text-center">Preference Form</h2>
                        <Form.Group className="mb-3">
                            <Form.Label>Who do you want to team up with:</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="Search users"
                                value={searchQuery}
                                onChange={handleSearch}
                            />
                        </Form.Group>
                        <ul className="list-group">
                            {filteredUsers.map((user) => (
                                user.email !== userData.email && (<li key={user.email} className="list-group-item d-flex justify-content-between align-items-center">
                                    {user.name}
                                    <Form.Check
                                        type="checkbox"
                                        checked={selectedUsers.includes(user.email)}
                                        onChange={() => handleSelectUser(user.email)}
                                    />
                                </li>)
                            ))}
                        </ul>
                        <Button className="mt-3" style={{backgroundColor: "#2463AB", color: "white"}} onClick={handleSubmit} disabled={selectedUsers.length !== maxSelection}>Submit</Button>
                        <p className="mt-2 text-muted">* Select {maxSelection} people that you to team up with.</p>
                    </Container>}
                    {session.preferenceGiven && session.status === "Ongoing" && <Container className="text-center">
                        <h3>You have already given your preferences</h3>
                    </Container>}
                    {
                        session.status === "Completed" && 
                        <Container className="mt-1 text-center" style={{margin:'0px auto', width:'70%'}}>
                            <Container>
                                <h2 className="text-center">Formed Groups</h2>
                                <Container>
                                    {
                                        groups?.map((group, index) => (
                                            <Container className="" key={index}>
                                                <h4>Group {index + 1}</h4>
                                                <ul className="list-group">
                                                    {group.map((user) => (
                                                        <li key={user} className="list-group-item d-flex justify-content-between align-items-center">
                                                            {user}
                                                        </li>
                                                    ))}
                                                </ul>
                                            </Container>
                                        ))
                                    }
                                </Container>
                            </Container>
                        </Container>
                    }
                </Container>
            </Container>
        </div>
    );
}
