import React, {useState, useEffect, useRef} from "react";

import { useNavigate, useParams } from "react-router-dom";
import { useContextApi } from "../ContexApi";
import { toast } from "react-toastify";
import { Button, Container, Modal, Table, Form } from "react-bootstrap";

import axios from "axios";

import logo from '../Assets/logo.PNG';

export default function AggregateDetails(){

    const {token, setToken, login, setLogin, userData, useLoclHost} = useContextApi();
    const [subjectDetails, setSubjectDetails] = useState([]);
    const navigate = useNavigate();
    const {id} = useParams();
    const [quizMarks, setQuizMarks] = useState({});
    const [assignmentMarks, setAssignmentMarks] = useState({});
    const [midMarks, setMidMarks] = useState({});
    const [finalMarks, setFinalMarks] = useState({});
    const [addMod, setAddMod] = useState(false);
    const [newActivity, setNewActivity] = useState({
        name: "",
        total_marks: 0,
        type: ""
    });
    

    const fetchSubjectDetails = () => {
        axios.get((useLoclHost?"http://localhost:2028":"")+"/api/subjects/"+id+"/").then((res) => {
            if (res.status == 200) {
                setSubjectDetails(res.data);
                console.log(res.data);
                let tempMarks = {};
                for (let i = 0; i < res.data.quizes.length; i++) {
                    tempMarks[res.data.quizes[i].quiz_id] = res.data.quizes[i].quiz_marks_obtained;
                }
                setQuizMarks(tempMarks);
                tempMarks = {};
                for (let i = 0; i < res.data.assignments.length; i++) {
                    tempMarks[res.data.assignments[i].assignment_id] = res.data.assignments[i].assignment_marks_obtained;
                }
                setAssignmentMarks(tempMarks);
                tempMarks = {};
                for (let i = 0; i < res.data.mids.length; i++) {
                    tempMarks[res.data.mids[i].mid_id] = res.data.mids[i].mid_marks_obtained;
                }
                setMidMarks(tempMarks);
                tempMarks = {};
                for (let i = 0; i < res.data.finals.length; i++) {
                    tempMarks[res.data.finals[i].final_id] = res.data.finals[i].final_marks_obtained;
                }
                setFinalMarks(tempMarks);
            }
        }).catch((err) => {
            console.log(err);
        });
    }

    const handleCombnedAggregateData = () => {
        let tempData = [];
    
        // Helper function to safely push items into tempData
        const addItem = (type, task, marksData, marksKey, nameKey, totalMarksKey) => {
            if (task && marksData.hasOwnProperty(task[marksKey]) && task[nameKey] && task[totalMarksKey]) {
                tempData.push({
                    subject_id: id,
                    subject_name: subjectDetails?.name || "Unknown Subject",
                    task_id: task[marksKey],
                    type: type,
                    name: task[nameKey],
                    marks: marksData[task[marksKey]] || 0, // Default to 0 if marks are missing
                    total_marks: task[totalMarksKey] || 0, // Default to 0 if total_marks are missing
                });
            }
        };
    
        // Process quizzes
        subjectDetails?.quizes?.forEach(quiz => {
            addItem("quiz", quiz, quizMarks, "quiz_id", "quiz_name", "quiz_marks");
        });
    
        // Process assignments
        subjectDetails?.assignments?.forEach(assignment => {
            addItem("assignment", assignment, assignmentMarks, "assignment_id", "assignment_name", "assignment_marks");
        });
    
        // Process mids
        subjectDetails?.mids?.forEach(mid => {
            addItem("mid", mid, midMarks, "mid_id", "mid_name", "mid_marks");
        });
    
        // Process finals
        subjectDetails?.finals?.forEach(final => {
            addItem("final", final, finalMarks, "final_id", "final_name", "final_marks");
        });
    
        return tempData;
    };

    const handleDelete = (taskId, type) => {
        axios.delete((useLoclHost?"http://localhost:2028":"")+"/api/subjects/activity/"+taskId+"/",{
            data: {
                taskType: type
            }
        })
        .then((res) => {
            if (res.status == 200) {
                fetchSubjectDetails();
                toast.success("Activity Deleted Successfully!");
            }
        })
        .catch((err) => {
            console.log(err);
        })
    }
    

    const handleCalculateAggregate = () => {
        const dataDetails= handleCombnedAggregateData();
        axios.post((useLoclHost?"http://localhost:2028":"")+"/api/calculate-aggregate/", {
            data: dataDetails
        })
        .then((res) => {
            if (res.status == 200) {
                toast.success(`Your Aggregate is ${res.data.aggregate}`);
            }
        })
        .catch((err) => {
            console.log(err);
        })
    }

    const handleChangesQuizMarks = (e) => {
        if (Number(e.target.value) > Number(e.target.max)) {
            console.log(e.target.value);
            e.target.value = e.target.max;
        }
        setQuizMarks({...quizMarks, [e.target.name]: e.target.value});
    }

    const handleChangesAssignmentMarks = (e) => {
        if (Number(e.target.value) > Number(e.target.max)) {
            console.log(e.target.value);
            e.target.value = e.target.max;
        }
        setAssignmentMarks({...assignmentMarks, [e.target.name]: e.target.value});
    }

    const handleChangesMidMarks = (e) => {
        if (Number(e.target.value) > Number(e.target.max)) {
            console.log(e.target.value);
            e.target.value = e.target.max;
        }
        setMidMarks({...midMarks, [e.target.name]: e.target.value});
    }

    const handleChangesFinalMarks = (e) => {
        if (Number(e.target.value) > Number(e.target.max)) {
            console.log(e.target.value);
            e.target.value = e.target.max;
        }
        setFinalMarks({...finalMarks, [e.target.name]: e.target.value});
    }

    const handleAddActivity = () => {
        axios.post((useLoclHost?"http://localhost:2028":"")+"/api/subjects/"+id+"/add-task/", {
            taskType: newActivity.type,
            taskName: newActivity.name,
            taskMarks: newActivity.marks
        }).then((res) => {
            if (res.status == 200) {
                toast.success("Activity Added Successfully!");
                setAddMod(false);
                fetchSubjectDetails();
            }
        }).catch((err) => {
            console.log(err);
        });
    }

    useEffect(() => {
        if (token){
            fetchSubjectDetails();

        }
    }, [token, login])



    return(
        <div style={{display:'flex', alignItems:'stretch', height:'100%'}}>
            <Container className="sidebar" style={{maxWidth: "100%", backgroundColor: "#2463AB", width: "20%", padding: '0 !important', margin:'0 !important'}}>
                <p className="h2 text-center mt-5" style={{color:'white'}}>Navigation</p>
                <Container className="sidebar-links">
                    <p className="nav-btn text-center" onClick={()=>navigate("/")}>Home</p>
                    {userData?.isAdmin && <p className="nav-btn text-center" onClick={()=>navigate("/users")}>Users</p>}
                    <p className="nav-btn text-center active" onClick={()=>navigate("/aggregate-calculator")}>Aggregate Calculator</p>
                    <p className="nav-btn text-center" onClick={()=> navigate("/group-former")}>Group Former</p>
                    <p className="nav-btn text-center" onClick={()=>navigate("/quiz-bank")}>Quiz Bank</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/your-profile")}>Your Profile</p>
                    <p className="nav-btn text-center" onClick={() => {setToken(null); localStorage.removeItem("token"); setLogin(false); navigate("/login")}}><strong>Logout</strong></p>
                </Container>
            </Container>
            <Container className="main" style={{padding:'0', margin:'0', minHeight:'100vh'}}>
                <Container className="header" style={{width:'100%', border:'1px solid black', padding: '0', margin:'0', maxHeight:'15%', height:'100px'}}>
                    <Container style={{padding: '20px', textAlign:'center', display:'flex', alignItems:'center', justifyContent:'center'}}>
                        <img src={logo} style={{width:'50px', height:'50px', marginRight:'10px'}} alt="logo" />
                        <p className="h1">StudentSync</p>
                    </Container>
                </Container>
                <Container className="mainContent" style={{backgroundColor:'#D4D4D4', minHeight:'100vh',display:'flex',flexDirection:'column'}}>
                    <Container className="text-center mt-4">
                        <h2>{subjectDetails?.name}</h2>
                        <p style={{fontSize:'20px'}}>Credit Hours: {subjectDetails?.credits}</p>
                    </Container>
                    <Container className="admin-btns" style={{display:'flex', justifyContent:'center', margin:'20px 0'}}>
                        {
                            userData?.isAdmin &&
                            <Button variant="primary" style={{backgroundColor:'#2463AB'}} onClick={() => setAddMod(true)}>+ New Graded Activity</Button>
                        }
                    </Container>
                    <Container className="mt-2">
                        <Container>
                            <h5 className="text-center">Quizes ({subjectDetails?.quiz_weightage}%)</h5>
                            <Table striped bordered hover responsive style={{width:'80%', margin:'10px auto', fontSize:'15px'}}>
                                <thead>
                                    <tr>
                                        <th>Quiz Id</th>
                                        <th>Quiz Name</th>
                                        <th>Total Marks</th>
                                        <th>Obtained Marks</th>
                                        <th>Percentage</th>
                                        {userData?.isAdmin && <th>Actions</th>}
                                    </tr>
                                </thead>
                                <tbody>
                                    {subjectDetails?.quizes?.map((quiz) => {
                                        return(
                                            <tr>
                                                <td>{quiz.quiz_id}</td>
                                                <td>{quiz.quiz_name}</td>
                                                <td>{quiz.quiz_marks}</td>
                                                <td>
                                                    <Form.Control type="number" max={quiz.quiz_marks} style={{width:'100px'}} name={quiz.quiz_id} value={quizMarks[quiz.quiz_id]} onChange={handleChangesQuizMarks} />
                                                </td>
                                                <td>
                                                    {quizMarks[quiz.quiz_id] / quiz.quiz_marks * 100}%
                                                </td>
                                                {userData?.isAdmin &&
                                                    <td>
                                                        <Button variant="danger" onClick={() => handleDelete(quiz.quiz_id, "quiz")}>Delete</Button>
                                                    </td>
                                                }
                                            </tr>
                                        )
                                    })}
                                </tbody>
                            </Table>
                        </Container>
                    </Container>
                    <Container>
                        <h5 className="text-center">Assignments ({subjectDetails?.assignment_weightage}%)</h5>
                        <Table striped bordered hover responsive style={{width:'80%', margin:'10px auto', fontSize:'15px'}}>
                            <thead>
                                <tr>
                                    <th>Assignment Id</th>
                                    <th>Assignment Name</th>
                                    <th>Total Marks</th>
                                    <th>Obtained Marks</th>
                                    <th>Percentage</th>
                                    {
                                        userData?.isAdmin &&
                                        <th>Actions</th>
                                    }
                                </tr>
                            </thead>
                            <tbody>
                                {subjectDetails?.assignments?.map((assignment) => {
                                    return(
                                        <tr>
                                            <td>{assignment.assignment_id}</td>
                                            <td>{assignment.assignment_name}</td>
                                            <td>{assignment.assignment_marks}</td>
                                            <td>
                                                <Form.Control type="number" max={assignment.assignment_marks} style={{width:'100px'}} name={assignment.assignment_id} value={assignmentMarks[assignment.assignment_id]} onChange={handleChangesAssignmentMarks} />
                                            </td>
                                            <td>
                                                {assignmentMarks[assignment.assignment_id] / assignment.assignment_marks * 100}%
                                            </td>
                                            {
                                                userData?.isAdmin &&
                                                <td>
                                                    <Button variant="danger" onClick={() => handleDelete(assignment.assignment_id, "assignment")}>Delete</Button>
                                                </td>
                                            }
                                        </tr>
                                    )
                                }
                                )}
                            </tbody>
                        </Table>

                    </Container>
                    <Container>
                        <h5 className="text-center">Mid Terms ({subjectDetails?.mids_weightage}%)</h5>
                        <Table striped bordered hover responsive style={{width:'80%', margin:'10px auto', fontSize:'15px'}}>
                            <thead>
                                <tr>
                                    <th>Mid Id</th>
                                    <th>Mid Name</th>
                                    <th>Total Marks</th>
                                    <th>Obtained Marks</th>
                                    <th>Percentage</th>
                                    {
                                        userData?.isAdmin &&
                                        <th>Actions</th>
                                    }
                                </tr>
                            </thead>
                            <tbody>
                                {subjectDetails?.mids?.map((mid) => {
                                    return(
                                        <tr>
                                            <td>{mid.mid_id}</td>
                                            <td>{mid.mid_name}</td>
                                            <td>{mid.mid_marks}</td>
                                            <td>
                                                <Form.Control type="number" max={mid.mid_marks} style={{width:'100px'}} name={mid.mid_id} value={midMarks[mid.mid_id]} onChange={handleChangesMidMarks} />
                                            </td>
                                            <td>
                                                {midMarks[mid.mid_id] / mid.mid_marks * 100}%
                                            </td>
                                            {
                                                userData?.isAdmin &&
                                                <td>
                                                    <Button variant="danger" onClick={() => handleDelete(mid.mid_id, "mid")}>Delete</Button>
                                                </td>
                                            }
                                        </tr>
                                    )
                                }
                                )}
                            </tbody>
                        </Table>
                    </Container>
                    <Container>
                        <h5 className="text-center">Finals ({subjectDetails?.finals_weightage}%)</h5>
                        <Table striped bordered hover responsive style={{width:'80%', margin:'10px auto', fontSize:'15px'}}>
                            <thead>
                                <tr>
                                    <th>Final Id</th>
                                    <th>Final Name</th>
                                    <th>Total Marks</th>
                                    <th>Obtained Marks</th>
                                    <th>Percentage</th>
                                    {
                                        userData?.isAdmin &&
                                        <th>Actions</th>
                                    }
                                </tr>
                            </thead>
                            <tbody>
                                {subjectDetails?.finals?.map((final) => {
                                    return(
                                        <tr>
                                            <td>{final.final_id}</td>
                                            <td>{final.final_name}</td>
                                            <td>{final.final_marks}</td>
                                            <td>
                                                <Form.Control type="number" max={final.final_marks} style={{width:'100px'}} name={final.final_id} value={finalMarks[final.final_id]} onChange={handleChangesFinalMarks} />
                                            </td>
                                            <td>
                                                {finalMarks[final.final_id] / final.final_marks * 100}%
                                            </td>
                                            {
                                                userData?.isAdmin &&
                                                <td>
                                                    <Button variant="danger" onClick={() => handleDelete(final.final_id, "final")}>Delete</Button>
                                                </td>
                                            }
                                        </tr>
                                    )
                                }
                                )}
                            </tbody>
                        </Table>
                        <Container className="text-center btn-container mt-3 mb-5">
                            <Button variant="success" onClick={handleCalculateAggregate}>Calculate Aggregate</Button>
                        </Container>
                    </Container>

                    <Container className="modal-container">
                        <Modal show={addMod} onHide={()=>setAddMod(false)}>
                            <Modal.Header closeButton>
                                <Modal.Title>Add New Activity</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <Form.Group className="mb-3">
                                        <Form.Label>Activity Name</Form.Label>
                                        <Form.Control type="text" value={newActivity.name} onChange={(e) => setNewActivity({...newActivity, name: e.target.value})} placeholder="Enter Activity Name" />
                                    </Form.Group>
                                    <Form.Group className="mb-3">
                                        <Form.Label>Total Marks</Form.Label>
                                        <Form.Control type="number" value={newActivity.marks} onChange={(e) => setNewActivity({...newActivity, marks: e.target.value})} placeholder="Enter Total Marks" />
                                    </Form.Group>
                                    <Form.Group className="mb-3">
                                        <Form.Label>Activity Type</Form.Label>
                                        <Form.Select value={newActivity.type} onChange={(e) => setNewActivity({...newActivity, type: e.target.value})}>
                                            <option>Select Type</option>
                                            <option value="quiz">Quiz</option>
                                            <option value="assignment">Assignment</option>
                                            <option value="mid">Mid Term</option>
                                            <option value="final">Final</option>
                                        </Form.Select>
                                    </Form.Group>
                                </Form>
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={()=>setAddMod(false)}>
                                    Close
                                </Button>
                                <Button variant="primary" onClick={handleAddActivity}>
                                    Add Activity
                                </Button>
                            </Modal.Footer>
                        </Modal>
                    </Container>
                </Container>
            </Container>
        </div>
    )
}