import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useContextApi } from "../ContexApi";
import { toast } from "react-toastify";
import { Button, Container, Card, Form, Modal } from "react-bootstrap";
import axios from "axios";

import logo from '../Assets/logo.PNG';

export default function QuizBank() {
    const { token, setToken, login, setLogin, userData, useLoclHost } = useContextApi();
    const [subjects, setSubjects] = useState([]);
    const [questions, setQuestions] = useState({});
    const [displayQuestions, setDisplayQuestions] = useState("");
    const [visibleAnswers, setVisibleAnswers] = useState({});
    const [newQuestion, setNewQuestion] = useState({ subject: "", question: "", answer: "" });
    const [showModal, setShowModal] = useState(false);  // State to control modal visibility
    const navigate = useNavigate();
    const [selectedSub, setSelectedSub] = useState("");

    const fetchSubjects = () => {
        axios.get((useLoclHost?"http://localhost:2028":"")+"/api/subjects/").then((res) => {
            if (res.status === 200) {
                setSubjects(res.data.subjects);
            }
        }).catch((err) => {
            console.log(err);
        });
    };

    const fetchQuestions = () => {
        axios.get((useLoclHost?"http://localhost:2028":"")+"/api/quiz/").then((res) => {
            setQuestions(res.data);
        });
    };

    useEffect(() => {
        if (token) {
            fetchSubjects();
            fetchQuestions();
        }
    }, [token, login]);

    const toggleAnswerVisibility = (id) => {
        setVisibleAnswers(prevState => ({
            ...prevState,
            [id]: !prevState[id],
        }));
    };

    // Function to handle adding a new question
    const handleAddQuestion = () => {
        // Remove commas from subject, question, and answer before sending
        const cleanedQuestion = {
            subject: newQuestion.subject.replace(/,/g, ""),
            question: newQuestion.question.replace(/,/g, ""),
            answer: newQuestion.answer.replace(/,/g, ""),
        };
        setShowModal(false);  // Close the modal after submission

        if (cleanedQuestion.subject && cleanedQuestion.question && cleanedQuestion.answer) {
            axios.post((useLoclHost?"http://localhost:2028":"")+"/api/quiz/", cleanedQuestion)
                .then((res) => {
                    if (res.status === 200) {
                        toast.success("Question added successfully!");
                        fetchQuestions(); // Refresh questions after adding
                        setNewQuestion({ subject: "", question: "", answer: "" }); // Reset form
                        
                    }
                })
                .catch((err) => {
                    console.error(err);
                    toast.error("Failed to add question.");
                });
        } else {
            toast.error("Please fill in all fields.");
        }
    };

    // Function to handle deleting a question
    const handleDeleteQuestion = (subject, id) => {
        axios.delete((useLoclHost?"http://localhost:2028":"")+"/api/quiz/", {
            data: { subject, id },
        })
            .then((res) => {
                if (res.status === 200) {
                    toast.success("Question deleted successfully!");
                    fetchQuestions(); // Refresh questions after deletion
                }
            })
            .catch((err) => {
                console.error(err);
                toast.error("Failed to delete question.");
            });
    };

    return (
        <div style={{ display: 'flex', alignItems: 'stretch', height: '100%' }}>
            <Container className="sidebar" style={{ maxWidth: "100%", backgroundColor: "#2463AB", width: "20%", padding: '0 !important', margin: '0 !important' }}>
                <p className="h2 text-center mt-5" style={{ color: 'white' }}>Navigation</p>
                <Container className="sidebar-links">
                    <p className="nav-btn text-center" onClick={() => navigate("/")}>Home</p>
                    {userData?.isAdmin && <p className="nav-btn text-center" onClick={() => navigate("/users")}>Users</p>}
                    <p className="nav-btn text-center" onClick={() => navigate("/aggregate-calculator")}>Aggregate Calculator</p>
                    <p className="nav-btn text-center" onClick={()=> navigate("/group-former")}>Group Former</p>
                    <p className="nav-btn text-center active" onClick={() => navigate("/quiz-bank")}>Quiz Bank</p>
                    <p className="nav-btn text-center" onClick={() => navigate("/your-profile")}>Your Profile</p>
                    <p className="nav-btn text-center" onClick={() => { setToken(null); localStorage.removeItem("token"); setLogin(false); navigate("/login") }}><strong>Logout</strong></p>
                </Container>
            </Container>
            <Container className="main" style={{ padding: '0', margin: '0', minHeight: '100vh' }}>
                <Container className="header" style={{ width: '100%', border: '1px solid black', padding: '0', margin: '0', maxHeight: '15%', height: '100px' }}>
                    <Container style={{ padding: '20px', textAlign: 'center', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <img src={logo} style={{ width: '50px', height: '50px', marginRight: '10px' }} alt="logo" />
                        <p className="h1">StudentSync</p>
                    </Container>
                </Container>
                <Container className="mainContent" style={{ backgroundColor: '#D4D4D4', minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
                    <Container className="text-center mt-4">
                        <h2>Your Courses</h2>
                        <p>Please select a course</p>
                    </Container>
                    <Container style={{ display: 'flex', justifyContent: 'center', flexWrap: 'wrap' }}>
                        {
                            subjects.map((subject, index) => (
                                <Card style={{ margin: '10px', minWidth: '500px' }} className="zoomable-card" onClick={() => setSelectedSub(subject.name)}>
                                    <Card.Header style={{ display: 'flex' }}>
                                        <Container>
                                            <Card.Title style={{ fontSize: '18px' }}>{subject.name}</Card.Title>
                                        </Container>
                                    </Card.Header>
                                </Card>
                            ))
                        }
                    </Container>
                    {selectedSub&&<Container className="text-center mt-4">
                        <h3>{selectedSub} (Questions)</h3>
                    </Container>}
                    <Container style={{ display: 'flex', justifyContent: 'center', flexWrap: 'wrap', flexDirection:'column', width:'70%' }}>
                        {
                            questions[selectedSub]?.split('\n').map((line, index) => {
                                if (!line) return null;
                                if (index === 0) return null;
                                const [id, question, answer] = line.split(',');
                                return (
                                    <Card style={{ margin: '10px', minWidth: '500px' }}  key={id}>
                                        <Card.Header style={{ display: 'flex' }}>
                                            <Card.Title style={{ fontSize: '18px' }}>{id}) {question}</Card.Title>
                                        </Card.Header>
                                        <Card.Footer>
                                            {visibleAnswers[id] && <p>{answer}</p>}
                                            <Button variant="secondary" size="sm" onClick={() => toggleAnswerVisibility(id)}>
                                                {visibleAnswers[id] ? "Hide Answer" : "View Answer"}
                                            </Button>
                                            {userData?.isAdmin && (
                                                <Button variant="danger" size="sm" onClick={() => handleDeleteQuestion(selectedSub, id)} style={{ marginLeft: '10px' }}>
                                                    Delete
                                                </Button>
                                            )}
                                        </Card.Footer>
                                    </Card>
                                );
                            })
                        }
                    </Container>

                    {userData?.isAdmin && (
                        <Container className="text-center mt-4">
                            <Button variant="primary" onClick={() => setShowModal(true)} style={{ backgroundColor: '#2463AB' }}>Add a New Question</Button>

                            {/* Modal for adding new question */}
                            <Modal show={showModal} onHide={() => setShowModal(false)}>
                                <Modal.Header closeButton>
                                    <Modal.Title>Add New Question</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    <Form>
                                        <Form.Group>
                                            <Form.Label>Subject</Form.Label>
                                            <Form.Control
                                                as="select"
                                                value={newQuestion.subject}
                                                onChange={(e) => setNewQuestion({ ...newQuestion, subject: e.target.value })}
                                            >
                                                <option value="">Select Subject</option>
                                                {subjects.map((subject, index) => (
                                                    <option key={index} value={subject.name}>{subject.name}</option>
                                                ))}
                                            </Form.Control>
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Label>Question</Form.Label>
                                            <Form.Control
                                                type="text"
                                                value={newQuestion.question}
                                                onChange={(e) => setNewQuestion({ ...newQuestion, question: e.target.value })}
                                            />
                                        </Form.Group>
                                        <Form.Group>
                                            <Form.Label>Answer</Form.Label>
                                            <Form.Control
                                                type="text"
                                                value={newQuestion.answer}
                                                onChange={(e) => setNewQuestion({ ...newQuestion, answer: e.target.value })}
                                            />
                                        </Form.Group>
                                    </Form>
                                </Modal.Body>
                                <Modal.Footer>
                                    <Button variant="secondary" onClick={() => setShowModal(false)}>Close</Button>
                                    <Button variant="primary" style={{ backgroundColor: '#2463AB' }} onClick={handleAddQuestion}>Add Question</Button>
                                </Modal.Footer>
                            </Modal>
                        </Container>
                    )}
                </Container>
            </Container>
        </div>
    );
}
